import Vue from 'vue';
import VueRouter from 'vue-router';
//

import BackLogin            from '@/components/backend/BackLogin';
import BackDashboard        from '@/components/backend/BackDashboard';
import BackProducts         from '@/components/backend/BackProducts';
import BackOrders           from '@/components/backend/BackOrders';
import BackCoupons          from '@/components/backend/BackCoupons';
import BackCustomerOrder    from '@/components/backend/BackCustomerOrder';
import BackCustomerCheckOut from '@/components/backend/BackCustomerCheckOut';

import FrontIndex           from '@/components/frontend/FrontIndex';
import FrontMarketing       from '@/components/frontend/pages/FrontMarketing';
import FrontMemberCart      from '@/components/frontend/pages/FrontMemberCart';
import FrontMemberCheckOut  from '@/components/frontend/pages/FrontMemberCheckOut';
import FrontCart            from '@/components/frontend/pages/FrontCart';
import FrontTeacherDetail   from '@/components/frontend/pages/FrontTeacherDetail';
import FrontContactUs       from '@/components/frontend/pages/FrontContactUs'; 
import FrontCalendar        from '@/components/frontend/FrontCalendar'; 

import FrontProductDetail   from '@/components/tools/OrderCard2/OrderCard2Detail'; 
import FrontCart2           from '@/components/tools/Cart2/FrontCart2';

Vue.use(VueRouter);

export default new VueRouter({
    routes:[
        {            
            path:'*',//*
            redirect:'',//
        },
        {
            name:'index',//
            path:'/index',//
            component:FrontIndex,//
            children:[
                {
                    name:'index2',//
                    path:'',//
                    component:FrontMarketing,//
                    props: true
                },
                // {
                //     name:'index',//
                //     path:'/trainer',//
                //     component:FrontTeacher,//
                //     props: true
                // },
                {
                    name:'index',//
                    path:'trainer/:trainerid',//
                    component:FrontTeacherDetail,//
                    props: true
                },
                {
                    name:'index',//
                    path:'calendar',//
                    component:FrontCalendar,//
                    props: true
                },                  
                // {
                //     name:'index',//
                //     path:'/divider',//
                //     component:FrontDivider,//
                //     props: true
                // },
                {
                    name:'index',//
                    path:'contactus',//
                    component:FrontContactUs,//
                    props: true
                },
                {
                    name:'index',//
                    path:'product/:productid',//
                    component:FrontProductDetail,//
                    props: true
                },
                {
                    name:'index',//
                    path:'fcart',//
                    component:FrontCart,//
                    props: true
                },
                {
                    name:'index',//
                    path:'membercart',//
                    component:FrontMemberCart,//
                    props: true
                },
                {
                    name:'index',//
                    path:'membercart/:productid',//
                    component:FrontMarketing,//
                    props: true
                },
                {
                    name:'index',//
                    path:'membercheckout/:orderId',//
                    component:FrontMemberCheckOut,//
                    props: true
                },
            ],            
        },
        {
            name:'',//
            path:'',//
            component:FrontIndex,//
            children:[
                {
                    name:'',//
                    path:'',//
                    component:FrontMarketing,//
                    props: true
                },
                // {
                //     name:'',//
                //     path:'/trainer',//
                //     component:FrontTeacher,//
                //     props: true
                // },
                {
                    name:'',//
                    path:'/trainer/:trainerid',//
                    component:FrontTeacherDetail,//
                    props: true
                },
                {
                    name:'',//
                    path:'calendar',//
                    component:FrontCalendar,//
                    props: true
                },                     
                // {
                //     name:'',//
                //     path:'/divider',//
                //     component:FrontDivider,//
                //     props: true
                // },
                {
                    name:'',//
                    path:'contactus',//
                    component:FrontContactUs,//
                    props: true
                },
                {
                    name:'',//
                    path:'product/:productid',//
                    component:FrontProductDetail,//
                    props: true
                },
                {
                    name:'',//
                    path:'fcart',//
                    component:FrontCart2,//
                    props: true
                },               
                {
                    name:'',//
                    path:'membercart',//
                    component:FrontMemberCart,//
                    props: true
                },
                {
                    name:'',//
                    path:'membercart/:productid',//
                    component:FrontMarketing,//
                    props: true
                },
                {
                    name:'',//
                    path:'membercheckout/:orderId',//
                    component:FrontMemberCheckOut,//
                    props: true
                },
            ],            
        },
        {
            name:'login',//
            path:'/login',//
            component:BackLogin,//
        },
        {
            name:'dashboard',//
            path:'/admin',//
            component:BackDashboard,//
            meta:{requiresAuth : true},            
            children:[
                {
                    name:'productsdefault',//
                    path:'',//
                    component:BackProducts,//
                    meta:{requiresAuth : true},
                }, 
                {
                    name:'products',//
                    path:'products',//
                    component:BackProducts,//
                    meta:{requiresAuth : true},
                },
                {
                    name:'orders',//
                    path:'orders',//
                    component:BackOrders,//
                    meta:{requiresAuth : true},
                },
                {
                    name:'coupons',//
                    path:'coupons',//
                    component:BackCoupons,//
                    meta:{requiresAuth : true},
                },

            ],
        },
        {
            name:'dashboard',//
            path:'/',//
            component:BackDashboard,//
            meta:{requiresAuth : true},            
            children:[
                {
                    name:'customerorder',//
                    path:'customerorder',//
                    component:BackCustomerOrder,//       
                },  
                {
                    name:'customercheckout',//
                    path:'customercheckout/:orderId',//
                    component:BackCustomerCheckOut,//       
                }, 
            ],
        },

   
    ],
});


