<template>
    <div>
        <loading :active.sync="isLoading"></loading>
        <div class="text-right">
            <button class="btn btn-success mt-4" 
                   @click="openModal(true)">
                    
            </button>
        </div>
        <table class="table mt-4">
            <thead>
                <tr>
                    <th>
                        
                    </th>
                    <th>
                        
                    </th>
                    <th>
                        
                    </th>
                    <th class="text-right">
                        
                    </th>     
                    <th class="text-right">
                        
                    </th>
                    <th class="text-right">
                        
                    </th>
                    <th>
                        
                    </th>    
                </tr>                                                                                     
            </thead>
            <tbody>
                <tr v-for="(item) in products" 
                   :key="item.id">
                    <td>
                        {{ item.num }}
                    </td>
                    <td>
                        {{ item.category }}
                    </td>
                    <td>
                        {{ item.title }}
                    </td>
                    <td class="text-right">
                        {{ item.origin_price | currency }}
                    </td>
                    <td class="text-right">
                        {{ item.price | currency }}
                    </td>
                    <td class="text-right">
                        {{ item.unit }}
                    </td>
                    <td>
                        <button 
                            class="btn btn-outline-primary btn-sm" 
                           @click="openModal(false,item)">
                           
                        </button>
                        <button 
                            class="btn btn-outline-danger btn-sm" 
                           @click="deleteProduct(item)">
                           
                        </button>
                    </td>
                </tr>
            </tbody>
        </table>
        <Pagin @postPage="getProducts"
               :getpagin="pagination">
        </Pagin>
        <!-- productModal -->
        <div id="productModal" 
             class="modal fade"  
             tabindex="-1" 
             role="dialog" 
             aria-labelledby="exampleModalLabel" 
             aria-hidden="true">
            <div class="modal-dialog" 
                 role="document">
                <div class="modal-content">
                <div class="modal-header">
                    <h5 id="exampleModalLabel" 
                        class="modal-title" >
                        Modal title
                    </h5>
                    <button type="button" 
                            class="close" 
                            data-dismiss="modal" 
                            aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <div class="row">
                        <div class="col-sm-4">
                            <div class="form-group">
                                <label for="image">
                                    
                                </label>
                                <input type="text" 
                                    class="form-control" 
                                    id="image"
                                    v-model="tempProduct.imageUrl"
                                    placeholder="">
                            </div>
                            <div class="form-group">
                                <label for="customFile"> 
                                    <i class="fas fa-spinner fa-pulse" 
                                    v-if="status.fileUploading">
                                    </i>
                                </label>
                                <input type="file" 
                                    id="customFile" 
                                    class="form-control"
                                    ref="files"
                                    @change="uploadFile">
                            </div>
                            <img img="https://images.unsplash.com/photo-1483985988355-763728e1935b?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=828346ed697837ce808cae68d3ddc3cf&auto=format&fit=crop&w=1350&q=80"
                                :src="tempProduct.imageUrl"
                                 class="img-fluid" 
                                 alt="">
                        </div>
                        <div class="col-sm-8">
                                    <div class="form-group">
                                            <label for="title">
                                                
                                            </label>
                                            <input type="text" 
                                                   class="form-control" 
                                                   id="title"
                                                   v-model="tempProduct.title"
                                                   placeholder="">
                                    </div>
                                    <div class="form-row">
                                        <div class="form-group col-md-6">
                                            <label for="category">
                                                
                                            </label>
                                            <input type="text" 
                                                   class="form-control" 
                                                   id="category"
                                                   v-model="tempProduct.category"
                                                   placeholder="">
                                        </div>
                                        <div class="form-group col-md-6">
                                            <label for="price">
                                                
                                            </label>
                                            <input type="unit" 
                                                   class="form-control" 
                                                   id="unit"
                                                   v-model="tempProduct.unit"
                                                   placeholder="">
                                        </div>
                                    </div>
                                    <div class="form-row">
                                        <div class="form-group col-md-6">
                                                <label for="origin_price">
                                                    
                                                </label>
                                                <input type="number" 
                                                    class="form-control" 
                                                    id="origin_price"
                                                    v-model="tempProduct.origin_price"
                                                    placeholder="">
                                        </div>
                                        <div class="form-group col-md-6">
                                                <label for="price">
                                                    
                                                </label>
                                                <input type="number" 
                                                    class="form-control" 
                                                    id="price"
                                                    v-model="tempProduct.price"
                                                    placeholder="">
                                        </div>
                                    </div>
                                    <hr>
                                    <div class="form-group">
                                            <label for="description">
                                                
                                            </label>
                                            <textarea type="text" 
                                                      class="form-control" 
                                                      id="description"
                                                      v-model="tempProduct.description"
                                                      placeholder="">
                                            </textarea>
                                    </div>
                                    <div class="form-group">
                                            <label for="content">
                                                
                                            </label>
                                            <textarea type="text" 
                                                      class="form-control" 
                                                      id="content"
                                                      v-model="tempProduct.content"
                                                      placeholder="">
                                            </textarea>
                                    </div>
                                    <div class="form-group">
                                            <div class="form-check">
                                                <input class="form-check-input" 
                                                       type="checkbox"
                                                       v-model="tempProduct.is_enabled"
                                                       :true-value="1"
                                                       :false-value="0"
                                                       id="is_enabled">
                                                <label class="form-check-label" 
                                                       for="is_enabled">
                                                       
                                                </label>
                                            </div>
                                    </div>
                        </div>
                    </div>                
                </div>
                <div class="modal-footer">            
                    <button type="button" 
                            class="btn btn-primary"
                           @click="updateProduct">
                        Save changes
                    </button>
                    <button type="button" 
                            class="btn btn-secondary" 
                            data-dismiss="modal">
                        Close
                    </button>
                </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import $        from 'jquery';
import Pagin    from '../tools/Pagin';

export default {
    components: {
        Pagin
    },
    data(){
        return {
            products            :[],
            tempProduct         :{},
            pagination          :{},
            isNew               :false,
            isLoading           :false,
            status:{
                fileUploading   :false,
            }
        }
    },
    methods:{
        getProducts(pagenum =1){
          const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products/?page=${pagenum}`;
          const vm  = this;
          vm.isLoading = true;
          this.$http.get(api).then((response) => {
                // console.log('=========');
                // console.log(response.data);
                // console.log('=========');
                vm.isLoading  = false;
                vm.products   = response.data.products;
                vm.pagination = response.data.pagination;
          });
        },
        openModal(isNew,item){
            //vuemodal
            //            
            if(isNew){
                this.tempProduct = {};
                this.isNew       = true;
            }
            else{
                //Object.assign
                this.tempProduct = Object.assign({},item);
                this.isNew       = false;
            }
            $('#productModal').modal('show');
        },
        updateProduct(){
            let api        = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product`;
            let httpMethod ='post';
            const vm       = this;          
                if(!vm.isNew){
                    // 
                    api        =  `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
                    httpMethod = 'put';
                }
                this.$http[httpMethod](api,{data:vm.tempProduct}).then((response) => {
                    // console.log('=========');
                    // console.log(response.data);
                    // console.log('=========');
                    if(response.data.success){
                        $('#productModal').modal('hide');
                        vm.getProducts();
                    }
                    else{
                        vm.getProducts();
                        console.log('');
                    }
            });
        },
        deleteProduct(item){
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products`;
            const vm  = this;
            var del   = confirm("?");
            if(del){
                //  this.$http.delete(api).then((response) => {
                //  console.log('=========');
                //  console.log(response.data);
                //  console.log('=========');
                //  vm.products=response.data.products;
                // });
            }
            else{

            }
        },
        uploadFile(){
            //console.log(this);
            const uploadedfile = this.$refs.files.files[0];
            const vm           = this;
            //
            const formData     = new FormData();
            //
            formData.append('file-to-upload',uploadedfile);
            const url          =`${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/upload`;
            // icon
            vm.status.fileUploading=true;
            //header
            this.$http.post(url,formData,{
                headers:{
                    'Content-Type':'multpart/form-data'
                }
            }).then((response) => {
                // console.log('=========');
                // console.log(response.data);
                // console.log('=========');
                // icon
                vm.status.fileUploading = false;
                if(response.data.success){
                     //vm.tempProduct.imageUrl = response.data.imageUrl;
                     //
                     vm.$set(vm.tempProduct,'imageUrl',response.data.imageUrl);
                }
                else{
                     this.$bus.$emit('message:push',response.data.message.message,'danger');
                }
            });
        }
    },
    created() {
        this.getProducts();
        //
        //this.$bus.$emit('message:push','','success');
    },
}
</script>


